import axios from 'axios'
import vue from 'vue'
import vuex from 'vuex'
// import socketStore from './socket-store'
import router from '../router'
// import plugins from './plugins'

let base = window.location.host.indexOf('localhost') > -1 ? '//localhost:8080/' : '/'

let api = axios.create({
    baseURL: base + 'api/',
    timeout: 2000,
    withCredentials: true
})

let auth = axios.create({
    baseURL: base,
    timeout: 2000,
    withCredentials: true
})

vue.use(vuex)

var store = new vuex.Store({
    state: {
        error: {},
        activeUser: {},
        keep: {},
        activeKeep: {},
        keeps: [],
        myKeeps: [],
        myCreatedKeeps: []
    },
    modules: {
        // socketStore
    },
    mutations: {

        //SET USER
        setUser(state, user) {
            state.activeUser = user
        },
        resetState(state) {
            state.error = {},
            state.activeKeep = {},
            state.keep = {},
            state.keeps = [],
            state.myKeeps = [],
            state.myCreatedKeeps = []
        },



        //HANDLE ERROR
        handleError(state, err) {
            state.error = err
        },


        //SET KEEPS
        addOrUpdateKeep(state, data) {

            var i = state.keeps.findIndex(e => e._id == data.keep._id)
            if (i > -1) {
                vue.set(state.keeps, i, data.keep)
            } else {
                state.keeps.push(data.keep)
            }

        },
        removeKeep(state, data) {
            for (var i = 0; i < state.keeps.length; i++) {
                var keep = state.keeps[i]
                if (keep._id == data.keep._id) {
                    state.keeps.splice(i, 1)
                    return
                }
            }
        },
        setKeeps(state, data) {
            state.keeps = data
            console.log(state.keeps)
        },
        setMyKeeps(state, data) {
            state.myEvents = data
            console.log(state.myEvents)
        },
        //COLLABORATORS
        setCollabs(state, data) {
            console.log(data)
            state.collaborators = data
        },
        setMyActivities(state, data) {
            state.myActivities = data
            console.log(state.myActivities)
        },
        setActiveEvent(state, data) {
            state.activeEvent = {}
            state.activeEvent = data
        },

        setActiveActivity(state, data) {
            vue.set(state, "activeActivity", data)
        },
        setTimeSlot(state, data) {
            state.timeSlots = data
        },
        // SET USER NOTES
        setUserNotes(state, data) {
            state.userNotes = data
        },

        // SET ACTIVE NOTE
        setActiveNote(state, note) {

            state.activeNote = {}
            state.activeNote = note
        },
        setAdminEvents(state, data) {
            console.log()
            state.adminEvents = data
        }

    },
    actions: {

        //***LOGIN AND REGISTER***

        //LOGIN

        login({ commit, dispatch }, payload) {

            auth.post('login', payload)
                .then(res => {
                    console.log("successful login")
                    commit('setUser', res.data.data)
                    dispatch('initSocket', res.data.data)
                })
                .catch(err => {
                    commit('handleError', err)

                })
        },

        //REGISTER

        register({ commit, dispatch }, payload) {

            auth.post('register', payload)
                .then(res => {
                    commit('setUser', res.data.data)
                    dispatch('initSocket', res.data.data)
                })
                .catch((err) => {
                    { commit('handleError', err) }
                })
        },

        //AUTHENTICATE

        authenticate({ commit, dispatch }) {
            auth('authenticate')
                .then(res => {
                    commit('setUser', res.data.data)
                    dispatch('initSocket', res.data.data)

                })
                .catch((err) => {
                    commit('handleError', err)
                })
        },

        //LOGOUT

        logout({ commit, dispatch }) {
            auth.delete('logout')
                .then((user) => {
                    user = {}

                    commit('setUser', user)
                    commit('resetState')
                    router.push({ name: 'Home' })
                })
        },

        //*** KEEPS/ACTIVITIES ***/

        //GET ALL KEEPS

        getAllEvents({ commit, dispatch }) {
            api('/find-keeps')
                .then(res => {
                    commit('setEvents', res.data.data)
                })
        },

        //GET ACTIVITIES BY KEEP ID

        getActivities({ commit, dispatch }, payload) {

            api('/keeps/' + payload._id + '/activities')
                .then(res => {
                    commit('setSchedule', res.data.data)
                })
                .catch(err => {
                    commit('handleError', err)
                })
        },

        //GET KEEPS BY LOCATION

        findEvents({ commit, dispatch }, location) {
            api(`/find-keeps/${location}`)
                .then(res => {
                    console.log('res to findEvents: ', res)
                    commit('setEvents', res.data.data)
                })
                .catch(err => {
                    commit('handleError', err)
                })
        },

        //GET MY KEEPS (USER)

        getMyEvents({ commit, dispatch }) {
            api(`/user-keeps`)
                .then(res => {
                    console.log('res to getMyEvents: ', res)
                    commit('setMyEvents', res.data.data)
                })
                .catch(err => {
                    commit('handleError', err)
                })
        },

        getMyActivities({ commit, dispatch }) {

            api(`/user-activities`)
                .then(res => {
                    console.log('res to getMyActivities: ', res)
                    commit('setUserSchedule', res.data.data)
                })
                .catch(err => {
                    commit('handleError', err)
                })
        },

        // CREATE NEW KEEP

        createEvent({ commit, dispatch }, payload) {

            api.post('keeps/', payload.keep)
                .then(res => {
                    console.log('res to create keep: ', res.data.data)
                    dispatch('getAllEvents')
                    dispatch('getCreatedEvents')
                    dispatch('addToMyEvents', { user: payload.user, keep: res.data.data })
                })
                .catch(err => {
                    commit('handleError', err)
                })
        },
        // ADD COLLABORATORS
        addCollab({ commit, dispatch }, payload) {
            api.put('add-collaborator/' + payload.keep._id, payload)
                .then(res => {
                    console.log(res)
                    dispatch('getCollabs', payload.keep)
                })
                .catch(err => {
                    commit('handleError', err)
                })
        },
        // GET COLLABORATORS
        getCollabs({ commit, dispatch }, keep) {
            console.log(keep)
            api('keeps/' + keep._id + '/collaborators')
                .then(res => {
                    console.log(res)
                    commit('setCollabs', res.data.data)
                })
                .catch(err => {
                    commit('handleError', err)
                })
        },
        //DELETE COLLABORATOR
        removeCollab({ commit, dispatch }, payload) {
            api.put('remove-collaborator/' + payload.keep._id, payload)
                .then(res => {
                    console.log('remove collab', res)
                    dispatch('getCollabs', payload.keep)
                })
                .catch(err => {
                    commit('handleError', err)
                })
        },
        // GET VAULT BY ID
        getActivityById({ commit, dispatch }, activity) {
            api('activities/' + activity._id)
                .then(res => {
                    commit('setActiveActivity', res.data.data)
                    dispatch('joinRoom', activity._id)
                })
                .catch(err => {
                    commit('handleError', err)
                })
        },

        // GET KEEP BY ID
        getEventById({ commit, dispatch }, keep) {

            api('keeps/' + keep._id)
                .then(res => {
                    commit('setActiveEvent', res.data.data)
                    dispatch('getActivities', { _id: keep._id })
                    dispatch('joinRoom', keep._id)
                })
                .catch(err => {
                    commit('handleError', err)
                })
        },

        // GET ADMIN KEEP BY ID
        getAdminEventById({ commit, dispatch }, keep) {
            api('admin-keeps/' + keep._id)
                .then(res => {
                    commit('setActiveEvent', res.data.data)
                    dispatch('getActivities', { _id: keep._id })
                    dispatch('joinRoom', keep._id)
                    if (!res.data.data) {
                        router.push(`/keep-schedule/${keep._id}`)
                    }
                })
                .catch(err => {
                    commit('handleError', err)
                })
        },

        // ADD VAULT
        addActivity({ commit, dispatch }, payload) {
            payload.activity.keepId = payload.keepId
            api.post('activities', payload.activity)
                .then(res => {
                    console.log(res)
                    dispatch('getActivities', { _id: payload.activity.keepId })
                    if (payload.emit) {
                        payload.action = 'getActivities'
                        dispatch('emitData', payload)
                    }
                })
                .catch(err => {
                    commit('handleError', err)
                })
        },

        addToMySchedule({ commit, dispatch }, payload) {
            payload.user.activities.push(payload.activity._id)
            api.put('/user-activities', payload.user)
                .then(res => {
                    console.log('activity has been added')
                    dispatch('authenticate')
                    if (payload.emit) {
                        payload.action = 'getActivityById'
                        dispatch('emitData', payload)
                    }
                })
                .catch(err => {
                    commit('handleError', err)
                })

        },
        editEvent({ commit, dispatch }, payload) {
            api.put('keeps/' + payload.keep._id, payload.keep)
                .then(res => {
                    commit('setActiveEvent', payload.keep)

                    if (payload.emit) {
                        payload.mutation = 'addOrUpdateEvent'
                        dispatch('emitData', payload)
                    }
                })
                .catch(err => {
                    commit('handleError', err)
                })
        },
        editActivity({ commit, dispatch }, payload) {

            api.put('activities/' + payload.activity._id, payload.activity)
                .then(res => {
                    commit('setActiveActivity', payload.activity)
                    dispatch('getActivities', { _id: payload.activity.keepId })
                    if (payload.emit) {
                        payload.action = 'getActivityById'
                        dispatch('emitData', payload)
                        payload.action = "getActivities"
                        dispatch('emitData', payload)
                    }
                })
                .catch(err => {
                    commit('handleError', err)
                })
        },

        // CREATE NOTE
        createNote({ commit, dispatch }, note) {

            api.post('notes', note)
                .then(res => {
                    console.log(res)
                    dispatch('getAllUserNotes')
                    dispatch('getNotebyNoteId', res.data.data)
                })
                .catch(err => {
                    commit('handleError', err)
                })
        },

        // GET ALL USER NOTES
        getAllUserNotes({ commit, dispatch }) {
            api.get('user-notes')
                .then(res => {
                    console.log(res)
                    commit('setUserNotes', res.data.data)
                })
        },

        //UPDATE NOTE
        updateNote({ commit, dispatch }, note) {
            api.put('notes/' + note._id, note)
                .then(res => {
                    commit('setUserNotes', note)
                    dispatch('getAllUserNotes', note)
                })
                .catch(err => {
                    commit('handleError', err)
                })
        },

        // GET NOTE BY NOTE ID
        getNotebyNoteId({ commit, dispatch }, note) {

            api('notes/' + note._id)
                .then(res => {
                    commit('setActiveNote', res.data.data)
                })
                .catch(err => {
                    commit('handleError', err)
                })
        },
        //DELETE NOTE
        deleteNote({ commit, dispatch }, note) {
            api.delete('notes/' + note._id)
                .then(res => {
                    dispatch('getAllUserNotes')
                })
                .catch(err => {
                    commit('handleError', err)
                })
        },
        deleteActivity({ commit, dispatch }, payload) {

            api.delete('activities/' + payload.activity._id)
                .then(res => {
                    console.log("delete request:", res)
                    dispatch('getActivities', { _id: res.data.data.keepId })
                    if (payload.emit) {
                        payload.action = 'getActivities'
                        dispatch('emitData', payload)
                        payload.action = 'getActivityById'
                        dispatch('emitData', payload)
                    }
                })
                .catch(err => {
                    commit('handleError', err)
                })
        },
        addToMyEvents({ commit, dispatch }, payload) {
            payload.user.keeps.push(payload.keep._id)

            api.put('/user-keeps', payload.user)
                .then(res => {
                    console.log('this keep has been added')
                    dispatch('authenticate')
                })
                .catch(err => {
                    commit('handleError', err)
                })
        },

        getMySchedule({ commit, dispatch }, keep) {
            api('/user-keeps/' + keep._id + '/activities')
                .then(res => {
                    console.log('getMySchedule', res)
                    commit('setUserSchedule', res.data.data)
                    commit('setActiveEvent', keep)
                })
                .catch(err => {
                    commit('handleError', err)
                })
        },

        //VARIOUS DELETES

        deleteEvent({ commit, dispatch }, payload) {

            api.delete('keeps/' + payload.keep._id)
                .then(res => {
                    console.log('res to delete keep: ', res.data.data)
                    router.push('/admin-keeps')
                    dispatch('getAllEvents')
                    if (payload.emit) {
                        payload.action = 'getAllEvents'
                        dispatch('emitData', payload)
                    }
                })
                .catch(err => {
                    commit('handleError', err)
                })
        },
        //REMOVE ACTIVITIES FROM MY SCHEDULE
        removeFromMySchedule({ commit, dispatch }, payload) {
            for (var i = 0; i < payload.user.activities.length; i++) {
                var activity = payload.user.activities[i]
                if (activity == payload.activity._id) {
                    payload.user.activities.splice(i, 1)
                    break
                }
            }
            api.put('/user-activities', payload.user)
                .then(res => {
                    console.log('activity has been removed from user schedule')
                    dispatch('authenticate')
                    dispatch('getMySchedule', payload.keep)
                })
                .catch(err => {
                    commit('handleError', err)
                })
        },

        getCreatedEvents({ commit, dispatch }) {
            api('/admin-keeps')
                .then(res => {
                    console.log('res to get created: ', res)
                    console.log('res.data.data of get created: ', res.data.data)
                    commit('setAdminEvents', res.data.data)
                })
                .catch(err => {
                    commit('handleError', err)
                })
        },
        //REMOVE KEEP FROM MY SCHEDULE
        removeFromMyEvents({ commit, dispatch }, payload) {
            api.put('/user-keeps/' + payload.keep._id, payload.keep)
                .then(res => {
                    console.log('this keep has been removed from user keeps')
                    dispatch('authenticate')
                    dispatch('getMyEvents')
                    dispatch('getMySchedule', payload.keep)
                })
                .catch(err => {
                    commit('handleError', err)
                })
        },
        publishEvent({ commit, dispatch }, payload) {
            api.put('/keeps/' + payload.keep._id, payload.keep)
                .then(res => {
                    dispatch('getAllEvents')
                    if (payload.emit && payload.keep.published) {
                        payload.mutation = 'addOrUpdateEvent'
                        dispatch('emitData', payload)
                    }
                    if (payload.emit && !payload.keep.published) {
                        payload.mutation = 'removeEvent'
                        dispatch('emitData', payload)
                    }
                })

                .catch(err => {
                    commit('handleError', err)
                })
        },

        //HANDLE ERROR
        handleError({ commit, dispatch }, err) {
            commit('handleError', err)
        }

    }

})


export default store


